<template>
    <div class="com-nav">
        <i class="glyphicon glyphicon-plus add" aria-hidden="true" @click="add"></i>
        <i class="glyphicon glyphicon-star "  :class="{collected:starred}" aria-hidden="true" @click="collect" ></i>
        <i class="glyphicon glyphicon-remove remove" aria-hidden="true" @click="remove"></i>
    </div>
</template>
<script>
import { mapState,mapMutations } from 'vuex'
export default {
    computed:{
        ...mapState({
            list: state => state.list,
            starred: state => state.collected
        })
    },
    methods:{
        ...mapMutations({
            add: 'addNote',
            remove: 'deleteNote',
            collect: 'collectNote'
        })
    }
    
}
</script>
<style scoped>
    .com-nav .collected::before{
        color:rgb(99, 238, 6);
    }
    .com-nav{
        position: absolute;
        top: 0;
        left: 0;
        width: 5%;
        height: 626px;
        background: rgb(40, 137, 228);
        text-align: center;
    }
    .glyphicon{
        width: 50px;
        height: 50px;
        margin: 10px 0;
    }
    .glyphicon::before{
        color: rgb(255, 255, 255);
        font-size: 30px;
    }
    .add{
        margin-top: 40px;
    }
</style>



