<template>
    <div class="content">
        <div class="list" v-for="(item,index) in list" :key="index">
            <input type="text" class="form-control" id="title" placeholder="Email" v-model="item.title" :class="{block:item.checked}">
            <textarea :class="{block:item.checked}"  class="form-control" cols="30" rows="10" v-model="item.content"></textarea>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    computed:{
        ...mapState({
            list: state => state.list
        })
    }
}
</script>
<style scoped>
    textarea{
        display: none;
        width: 100%;
        height: 600px;
    }
    input{
        display: none;
        width: 100%;
        height: 26px;
        font-size: 26px
    }
    .block{
        display: block;
    }
    .content{
        position: absolute;
        right: 0;
        width: 70%;
        height: 626px;
        /* background: gray; */

    }
</style>



