<template>
    <div class="title">
        <div class="list-header">
            <h2>NOTES | COLIGO</h2>
            <div class="btn-group btn-group-justified" role="group" aria-label="...">
                <div class="btn-group" role="group">
                    <button type="button" class="btn btn-default" @click="showall">All Notes</button>
                </div>
                <div class="btn-group" role="group">
                    <button type="button" class="btn btn-default" @click="showcollect">Collect</button>
                </div>
            </div>
        </div>
        <div class="list" v-for="(item,index) in list" :key="index" :class="{checked:item.checked}" @click="check(index)">
            <!-- 把被点击的数据索引传给store -->
            <label for="list" >
                <!-- @dblclick="editTitle(index)" -->
                <span>{{ item.title }}
                    <!-- {{item.checked}}{{index}} -->
                </span>
                <!-- <input type="text" name="list" :class="{block:item.edit}" v-model="item.title" @keyup.enter="editFinish"> -->
            </label>
        </div>
    </div>
</template>
<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
    computed:{
        ...mapState({
            list: state => state.list
        }),
        // ...mapGetters({
        //     showall: 'showall',
        //     showcollect: 'showcollect'
        // })
    },
    methods:{
        
        ...mapMutations({
            check: 'check',
            showall: 'showall',
            showcollect: 'showcollect'
            // editTitle: 'editTitle',
            // editFinish: 'editFinish'
        })
    }
}
</script>
<style scoped>
    .list input{
        display: none;
    }
    .list .block{
        display: block;
    }
    .checked{
        background: rgb(134, 134, 224);
    }
    li{
        list-style: none;
    }
    .title{
        position: absolute;
        top: 0%;
        left: 5%;
        width: 25%;
        text-align: center;
        background: rgba(231, 212, 212, 0.473)
    }
    .list-header {
        margin: 30px;
    }
    .list-header > h2 {
        margin: 20px;
        font-size: 22px;
        font-weight: inherit;
    }
    .title .list {
        margin: 0;
        padding: 0 30px;
        text-align: left;
        vertical-align: center;
    }
    .list input{
        width: 100%;
        height: 30px;
    }
</style>



