import Vue from 'vue'
import Vuex from 'vuex';

Vue.use(Vuex);
export default new Vuex.Store({
    state:{
        list:[
            {
                title: 'New Note',
                content: '66666',
                checked: false,
                collect: false
            },
            {
                title: 'New Note',
                content: '777777',
                checked: false,
                collect: false
            }
        ],
        listall: this.list,
        collected: false
    },
    // getters: {
        // showall(){
        //     return state.list.filter(function (val) {
        //         return val.collect === true || val.collect === false;
        //     })
        // },
        // showcollect() {
        //     return state.list.filter(function (val) {
        //         return val.collect === true;
        //     })
        // }
    // },
    mutations: {
        showall(state){
            state.list = state.listall
        },
        showcollect(state) {
            state.listall = state.list;
            state.list =  state.list.filter(function (val) {
                return val.collect === true;
            })
        },
        addNote(state) {
            // if(state.list === []){
                state.list.push({
                    title: 'New Note',
                    content: '',
                    checked: false,
                    collect: false
                })
            // }else{
                // state.list.push({
                //     title: 'New Note',
                //     content: '',
                //     checked: false,
                //     collect: false
                // })
            // }
            
        },
        check(state,index) {
            //取消所有数据的 选定 
            state.list.map(function (val) {
                val.checked = false;
                // if(val.collect === true ){
                //     state.collected = true;
                // }
            })
            //将选定的数据 checked改变 来改变class是否添加
            state.list[index].checked = true;
                if(state.list[index].collect === true){
                    state.collected = true;
                }else{
                    state.collected = false;
                }

        },
        deleteNote(state){
            //选出被选中的数据
            var c = state.list.filter(function (val) {
                return val.checked === true;
            })
            //获取 选中数据所在的索引
            var count = state.list.indexOf(c[0]);
            //删除
            if(count > -1){
                state.list.splice(count,1);
            }
        },
        collectNote(state){
            //选出被选中的数据
            var b = state.list.filter(function (val) {
                return val.checked === true;
            })
            //获取 选中数据所在的索引
            var bcount = state.list.indexOf(b[0]);
            console.log(state.list[bcount])

            if(bcount > -1){
                if(b[0].collect === true){
                    b[0].collect = false;
                }else{
                   b[0].collect = true; 
                }
            }
            state.list.map(function (val) {
                console.log(val)
                if(val.collect == true ){
                    state.collected = true;
                }else{
                    state.collected = false;
                }
            })

        }
        // editTitle(state,num){
        //     //取消所有数据的 选定 
        //     state.list.map(function (val) {
        //         val.edit = false;
        //     })
        //     //将选定的数据 edit改变 来改变class是否添加
        //     state.list[num].edit = true;
        // },
        // editFinish(state){
        //     state.list.map(function (val) {
        //         val.edit = false;
        //     })
        // }
    },
    
})


